/*

COLORS:
  #344E5C dark
  #4AB19D teal
  #EFC958 yellow
  #E17A47 orange
  #EF3D59 red

*/

*,
*::before,
*::after {
  box-sizing: border-box;
}


/* GLOBALS */
* {
  font-family: 'Karla', Verdana, Geneva, Tahoma, sans-serif; 
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


/* MAIN VIEW FORMATTING */

.main-container {
  display: flex;
}

.left-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
  width: 50vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #344E5C;
  background-image: url('../resources/argyle.png');
}

.right-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: auto;
  width: 50vw;
  transform: translateX(100%);
  color: #344E5C;
}


/* LEFT SIDE FORMATTING AND STYLES */
.left-container {
  color: #fff;
}

.tag {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tag h1 {
  margin: 0;
}

.tag span {
  font-family: 'Rubik', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: #4AB19D;
}

.nav-wrapper {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.nav-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-container a,
.nav-wrapper a {
  margin-bottom: 36px;
}

.nav-wrapper a {
  width: 35%;
  text-align: center;
  color: #fff;
  border: 2px solid;
  border-radius: 5px;
  padding: 24px 48px;
  font-size: 1.5em;
  text-decoration: none;
  background-color: transparent;
  transition: background-color 0.25s ease-in-out;
  position: relative;
}

.nav-wrapper a:hover {
  background-color: hsla(0, 0%, 100%, 0.2);
}

.is-active::after {
  background-color: #4AB19D;
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 6px;
  border-radius: 0 0 2.5px 2.5px;
}

.socials-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 75%;
}

.socials-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.icons-wrapper {
  display: flex;
  align-items: center;
  height: 2em;
}

.icons {
  font-size: 2em;
}

.socials-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  position: relative;
}

.socials-header h3 {
  margin: 0;
}

.socials-header hr {
  width: 100%;
  border-bottom: 2px solid #4AB19D;
}

/* RIGHT SIDE FORMATTING AND STYLES */

.page-container {
  height: auto;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 30px;
}

.page-container hr {
  width: 40%;
  border-top: 6px solid #344E5C;
  margin-top: 0;
}

.content-container {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.content-container hr {
  width: 40%;
  border-top: 3px solid #344E5C;
  margin: 0 auto auto 0;
}

.about-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.about-text {
  width: 60%;
  font-size: 1.25em;
}

.profile-container {
  width: auto;
  height: auto;
  position: relative;
}

.profile {
  width: 200px;
  height: auto;
  margin-right: 20px;
  position: relative;
  border-radius: 5px;
}

.profile-container::after {
  top: 20px;
  left: 20px;
  position: absolute;
  width: 200px;
  height: 266px;
  content: "";
  z-index: -1;
  border: 2px solid #4AB19D;
  border-radius: 5px;
}

.tech-list {
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-size: 1.25em;
}

.tech-list ul {
  width: 50%;
  list-style: outside none none;
  padding: 0;
  margin: 25px 0 0;
}

.tech-list li {
  margin-bottom: 20px;
  padding-left: 30px;
  position: relative;
}

.tech-list li::before {
  content: "â–¹";
  position: absolute;
  left: 0;
  font-size: 1.25em;
  line-height: .9em;
  color: #4AB19D;
}

.project-card {
  width: 100%;
  margin-bottom: 24px;
}

.project-details {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.project-img-wrapper {
  width: 55%;
  height: 100%;
  position: relative;
  border-radius: 5px;
}

.project-img-wrapper::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: hsla(168, 41%, 49%, 0.4);
  border-radius: 5px;
  transition: background-color .25s ease-in-out;
  z-index: 2;
}

.project-img-wrapper:hover::after {
  background: transparent;
}

.project-img {
  width: 100%;
  height: 100%;
  z-index: 1;
  border-radius: 5px;
}

.project-description {
  width: 40%;
  z-index: 3;
  display: flex;
  flex-direction: column;
  height: inherit;
  justify-content: center;
  text-align: right;
}

.project-description-right {
  width: 40%;
  z-index: 3;
  display: flex;
  flex-direction: column;
  height: inherit;
  justify-content: center;
  text-align: left;
}

.project-description h2,
.project-description-right h2 {
  margin: 0;
}

.details-p {
  background-color: #344E5C;
  margin-left: -50%;
  color: #fff;
  border-radius: 5px;
  padding: 16px;
  font-size: 1.25em;
}

.details-p-right {
  background-color: #344E5C;
  margin-right: -50%;
  color: #fff;
  border-radius: 5px;
  padding: 16px;
  font-size: 1.25em;
}

.tech-used {
  margin: 0;
}

.icon-links {
  margin-top: 1em;
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.icon-links-right {
  margin-top: 1em;
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

.project-icons {
  text-decoration: none;
  color: #344E5C;
  font-size: 2em;
  margin-left: 32px;
  transition: all .25s ease-in-out;
}

.project-icons-right {
  text-decoration: none;
  color: #344E5C;
  font-size: 2em;
  margin-right: 32px;
  transition: all .25s ease-in-out;
}

.project-icons:hover,
.project-icons-right:hover {
  color: #4AB19D;
}

/** MOBILE STYLES */
@media only screen and (max-width: 1600px) {
  .nav-wrapper a {
    font-size: 1.35em;
    padding: 24px 36px;
  }
}

@media only screen and (max-width: 1500px) {
  .profile {
    width: 175px;
  }

  .profile-container::after {
    width: 175px;
    height: 233px;
  }

  .socials-icons {
    flex-direction: column;
  }

  .icons {
    font-size: 1.5em;
  }
}

@media only screen and (max-width: 1400px) {
  .content-container {
    width: 90%;
  }

  .nav-wrapper a {
    font-size: 1.2em;
    padding: 24px 0px;
  }
}

.mobile-container {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.mobile-page-container {
  height: 100%;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 30px;
  position: relative;
}

.mobile-page-container hr {
  width: 40%;
  border-top: 6px solid #344E5C;
  margin-top: 0;
}

@media only screen and (max-width: 1300px) {
  .left-container {
    width: 100%;
    height: 100%;
  }

  .profile {
    width: 300px;
  }

  .profile-container::after {
    width: 300px;
    height: 400px;
  }
}

@media only screen and (max-width: 1000px) {
  .profile {
    width: 200px;
  }

  .profile-container::after {
    width: 200px;
    height: 266px;
  }
}

@media only screen and (max-width: 700px) {
  .profile {
    width: 175px;
  }

  .profile-container::after {
    width: 175px;
    height: 233px;
  }

  .tech-list {
    margin-bottom: 36px;
  }

  .nav-wrapper a {
    width: 60%;
  }
  
  .nav-container {
    flex-direction: column;
  }
}

@media only screen and (max-width: 600px) {
  .content-container {
    display: block;
  }

  .about-wrapper {
    align-items: center;
  }

  .project-details {
    display: block;
    margin-bottom: 48px;
  }

  .project-img-wrapper {
    height: 100%;
    width: 80%;
    margin: 24px auto;
  }

  .project-description,
  .project-description-right {
    width: 90%;
    margin: 0 auto;
  }

  .details-p {
    margin-left: 0;
    font-size: 1.1em; 
  }

  .tag h1 {
    font-size: 1.75em;
  }
}

@media only screen and (max-width: 500px) {
  .about-wrapper {
    flex-direction: column;
  }

  .about-wrapper {
    margin-bottom: 48px;
  }

  .about-text {
    width: 100%;
    margin-bottom: 24px;
  }

  .project-img-wrapper {
    height: 100%;
    width: 100%;
  }

  .tag h1 {
    font-size: 1.25em;
  }
}

@media only screen and (max-height: 800px) {
  .content-container {
    display: block;
    margin: 24px auto;
  }

  .nav-container {
    flex-direction: column;
  }

  .nav-container a {
    margin-bottom: 24px;
  }
}

@media only screen and (max-height: 600px) {
  .tag {
    margin-top: -32px;
  }

  .nav-wrapper {
    margin-top: -32px;
  }
}

.mobile-menu {
  height: 100vh;
  position: fixed; 
  z-index: 5; 
  left: 0;
  top: 0;
  overflow-x: hidden;
  transition: 0.25s ease-in-out;
  color: #fff;
  background-color: #344E5C;
  background-image: url('../resources/argyle.png');
}

.mobile-menu-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
  width: inherit;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.mobile-menu-button {
  background-color: #4AB19D;
  border-radius: 100%;
  color: #fff;
  position: absolute;
  width: 48px;
  height: 48px;
  text-align: center;
  line-height: 48px;
  font-size: 24px;
  top: 16px;
  right: 16px;
  z-index: 4;
}

.shadow {
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -moz-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}